:root {
  --font-montserrat: Montserrat, Helvetica, sans-serif;
}

.footer-note {
  font-family: var(--font-montserrat);
  flex-direction: row;
  padding: 1rem 0;
  display: flex;
  position: relative;
  overflow: hidden;
}

.update-container {
  flex: 1;
  align-items: center;
  padding-left: 6rem;
}

.resources-container {
  flex: 1;
  align-items: center;
  padding-left: 5rem;
}

.help-container {
  align-items: left;
  flex-direction: column;
  flex: .75;
  display: flex;
}

.note-info {
  width: 245px;
  font-size: small;
  display: flex;
}

.note-help, .note-contact, .note-order {
  font-size: small;
  display: flex;
}

@media screen and (width <= 479px) {
  h3 {
    font-size: 10px;
  }

  .note-info {
    width: 100px;
    display: flex;
  }

  .note-info, .note-help, .note-contact, .note-order {
    font-size: 8px;
  }

  .update-container, .resources-container {
    flex: 1;
    align-items: center;
    padding-left: 1rem;
  }

  .imgNote {
    width: 100px;
  }
}

@media screen and (width >= 480px) {
  h3 {
    font-size: 14px;
  }

  .note-info {
    width: 150px;
    display: flex;
  }

  .note-info, .note-help, .note-contact, .note-order {
    font-size: 12px;
  }

  .update-container {
    flex: 1;
    align-items: center;
    padding-left: 3rem;
  }

  .resources-container {
    flex: 1;
    align-items: center;
    padding-left: 1rem;
  }

  .imgNote {
    width: 120px;
  }

  .footer-note {
    margin-bottom: 10rem;
  }
}

@media screen and (width >= 1025px) {
  h3 {
    font-size: 14px;
  }

  .note-info {
    width: 150px;
    display: flex;
  }

  .note-info, .note-help, .note-contact, .note-order {
    font-size: 12px;
  }

  .update-container {
    flex: 1;
    align-items: center;
    padding-left: 3rem;
  }

  .resources-container {
    flex: 1;
    align-items: center;
    padding-left: 1rem;
  }

  .imgNote {
    width: 120px;
  }

  .footer-note {
    padding-left: 5rem;
  }
}
